<template>
  <div>
      <h3>Screenshots <span>ðŸ“·</span></h3>
      <slot></slot>
      <div class="preview" ref="preview"></div>
      <div class="form">
          <label for="screenshot">Screenshots</label>
          <input type="file" name="screenshot" id="screenshot" multiple ref="ss" @change="changed" accept="image/*">
      </div>
      <hr>

  </div>
</template>

<script>
import Compressor from 'compressorjs'
import reader from '../scripts/FileReader'
export default {

  methods:{
      changed(e){
          let list = Array(...e.target.files)
          this.files = list
          let prev = this.$refs.preview
          if(prev.children.lenght!=0){
              let childs = Array(...this.$refs.preview.children)
              childs.forEach(i=>{
                  prev.removeChild(i)
              })
          }
          list.forEach(i=>{
              let img = new Image()
              let w = window.innerWidth*.8
              reader(i).then(data=>{

                  img.onload = ()=>{
                      let ratio = img.width/img.height
                      if(img.width>w){
                          img.width = w
                          img.height = w/ratio
                          img.setAttribute('alt', 'screenshot')
                          img.style.marginTop = '.5rem'
                      }
                  }
                  img.src = data
                  prev.appendChild(img)
              })
          })
          this.result = []
          let count = 1
          let self =this
          list.forEach(i => {
             let img = new Image()
             let h =0
             let w = 0
             reader(i).then(data=>{

                  img.onload = ()=>{
                      h=img.height
                      w=img.width
                  }
                  img.src = data
              })

             new Compressor(i, {
                quality:0.8,
                mimeType: 'image/webp',
                strict: false,
                checkOrientation: false,
                convertSize:Infinity,
                success(blob){
                    blob.arrayBuffer()
                    .then(buf=>{
                        self.result.push({
                            name: `${self.dir}/screenshot-${count}.webp`,
                            data: new Uint8Array(buf),
                            size: `${h}x${w}`
                        })
                        count+=1 
                    })
                },
                
            })     
          });

      },



  },

  data(){
      return{
          files:[],
          result:[],
          dir: 'img/screenshot'
      }
  },
  props:{
      done:{
          type:Boolean,
          default: false
      }
  },



  watch:{

      result(val){
          if(val.length == this.files.length ) this.$emit('complete', val)
      },
      
  }

}
</script>

<style scoped>

section{
    margin: 1rem 0;
}
.preview>*{
    width: 100px;
    height: auto;
}

</style>