<template>
  <div id="app">
    <header class="header">
      <h2><span>ðŸ“¦</span> Web Essential</h2>
    </header>
    <main>
      <div class="progress" v-if="progress">
      <h3>{{textProgress}}</h3>
      <progress></progress>
    </div>
    <div v-else>
          <section>
      <h3>Basic Info <span>ðŸ“</span></h3>
      <div class="error" v-if="basicError" ><span>âš </span> Please fill all the field</div>
      <basic-info :done="done" @complete="getBasic"/>
      <div class="form border">
        <div class="modifier">
          <label for="usess">Screenshots</label>
          <input type="checkbox" name="usess" id="usess" v-model="useScreenshot">
        </div>
        <div class="modifier">
          <label for="usesc">Shortcuts</label>
          <input type="checkbox" name="usesc" id="usesc" v-model="useShortcut">
        </div>
        <div class="modifier">
          <label for="userel">Related App</label>
          <input type="checkbox" name="userel" id="userel" v-model="useRelApp">
        </div>
        
      </div>
      <hr>
    </section>
    
    <section>
      <h3>Icons <span>â­</span></h3>
      <div class="error" v-if="iconError" ><span>âš </span> Was it really an icon? or maybe try another one</div>
      <div v-if="file" style="text-align:center;">
        <div class="preview">
          <h4>Forced Square Preview</h4>
        </div>
      <img src="" alt="preview" height="128" width="128" ref="preview">
    </div>
    <div class="form">
      <label for="image">Select Icon</label>
      <input type="file" name="image" id="image" accept="image/*" @change="getImageFile">
    </div>
    <hr v-if="file">
    </section>
    <section>
      <android-icon-processor :file="file" @complete="getAndIcon" :done="done"/>
    </section>
    <section>
      <apple-icon-processor :file="file" @complete="getAppIcon" :done="done"/>
    </section>
    <section>
      <windows-icon-processor :file="file" @complete="getWinIcon" :done="done"/>
    </section>
    <favicon-processor :file="file" @complete="getFavIcon" :done="done"/>

    <section v-if="useRelApp">
      <related-app @complete="getRel">
         <div class="error" v-if="relError"><span>âš </span> You need to fill the fields</div>
      </related-app>
    </section>

    <section v-if="useScreenshot">
      <screensot-handler @complete="getss" :done="done" >
        <div class="error" v-if="ssError"><span>âš </span> Something went wrong here, Try another file</div>
      </screensot-handler>
    </section>

    <section  v-if="useShortcut">
      <shortcuts-handler @complete="getsc" :done="done">
        <div class="error" v-if="scError"><span>âš </span> Can not process this action</div>
      </shortcuts-handler>
      <hr>
    </section>
    
    

    <button @click="process" class="btn complete">Generate</button>
    </div>
    </main>
    <footer style="text-align:right; margin-right:1rem; margin-bottom:.5rem;">
      <small class="message" @click="showMessage=!showMessage">{{showMessage?'ðŸ”¼ Hide Message': 'ðŸ”½ Show Message'}}</small>
      <div :class="showMessage?'':'hide'">
        <div>
            <small style="background-color:#fcfc63">Every procces is running in the browser, no server communication</small>
        </div>
