          <small><a href="https://fengyuanchen.github.io/compressorjs/" target="_blank" rel="noopener noreferrer">ðŸ›  Compressor.js&#9;</a></small>
          <small><a href="https://github.com/jankovicsandras/imagetracerjs" target="_blank" rel="noopener noreferrer">ðŸ›  Imagetracer.js&#9;</a></small>
          <small><a href="https://stuk.github.io/jszip/" target="_blank" rel="noopener noreferrer">ðŸ›  JSZip&#9;</a></small>
          <small><a href="https://github.com/eligrey/FileSaver.js" target="_blank" rel="noopener noreferrer">ðŸ›  FileSaver.js&#9;</a></small>
        </div>
        <div>
          <small>ðŸ˜¢ Sorry for that ugly interface</small>
        </div>
      </div>
    </footer>
    

  </div>
