<script>
import AndroidIconProcessor from './components/AndroidIconProcessor.vue'
import AppleIconProcessor from './components/AppleIconProcessor.vue'
import BasicInfo from './components/BasicInfo.vue'
import FaviconProcessor from './components/FaviconProcessor.vue'
import ScreensotHandler from './components/ScreensotHandler.vue'
import ShortcutsHandler from './components/ShortcutsHandler.vue'
import RelatedApp from './components/RelatedApp.vue'
import {objectify_shortcut, objectify_screenshot, objectify_image} from './scripts/Objectify'
import {generate} from './scripts/TagGenerator'
import reader from './scripts/FileReader'
import Zip from 'jszip'
import {saveAs} from 'file-saver'
import WindowsIconProcessor from './components/WindowsIconProcessor.vue'

export default {
  components: {AndroidIconProcessor, BasicInfo, AppleIconProcessor, FaviconProcessor, ScreensotHandler, ShortcutsHandler, WindowsIconProcessor, RelatedApp },
  name: 'App',
  data(){
    return{
      file:undefined,
      done:false,
      progress: false,
      basicData:undefined,
      faviconData:undefined,
      appIconData:undefined,
      andIconData:undefined,
      winIconData:undefined,
      ssData: undefined,
      scData: undefined,
      relAppData: undefined,
      textProgress: '',
      useScreenshot: false,
      useShortcut: false,
      useRelApp: false,
      submit: false,
      showMessage: false
    }
  },

methods:{

  process(){
    this.submit = true
    this.progress = true
    this.textProgress = 'Working...'
    let self = this
    setTimeout(()=>{
      self.writeToArchive()
    }, 1000)
  },

  getImageFile(e){
    this.file =e.target.files[0]

  },

  getBasic(val){
    this.basicData = val
  },

  getRel(val){
    this.relAppData=val
  },

  getFavIcon(val){
    this.faviconData = val
  },

  getAppIcon(val){
    this.appIconData = val
  },

  getWinIcon(val){
    this.winIconData = val
  },

  getAndIcon(val){
    this.andIconData = val
  },

  getss(val){
    this.ssData = val
  },

  getsc(val){
    this.scData = val
  },


  collectingManifestData(){
     let manifest = {...this.basicData, ...objectify_image(this.andIconData)}
    if(this.useScreenshot){
      let holder = manifest
      manifest = {...holder, ...objectify_screenshot(this.ssData)}
    }
    if(this.useShortcut){
      let holder = manifest
      manifest = {...holder, ...objectify_shortcut(this.scData)}
    }
    if(this.useRelApp){
      let holder = manifest
      manifest = {...holder, ...this.relAppData}
    }
    return {
      name: 'manifest.json',
      data: JSON.stringify(manifest)

    }

  },

  generateHTML(){
    let data = [...this.faviconData, ...this.appIconData, ...this.winIconData]
    let manifest = this.collectingManifestData()
    data = [...data, manifest]
    let html = generate(data, this.basicData)
    return {
      name: 'index.html',
      data: html
    }
  },

  writeToArchive(){
    if (this.error) {
      this.progress = false
      return
      
    }

    this.textProgress = 'Generating Manifest'
    let manifest = this.collectingManifestData()

    this.textProgress = 'Generating HTML Tag'
    let html = this.generateHTML()


    this.textProgress = 'Creating Virtual Archive'
    let zip = new Zip()

    zip.file(manifest.name, manifest.data)

    zip.file(html.name, html.data)


    this.textProgress = 'Collecting Icons'
    let files = [...this.andIconData, ...this.appIconData, 
                ...this.winIconData, ...this.faviconData]

    if (this.useScreenshot){
      this.textProgress = 'Collecting Screenshots'
      files = [...files, ...this.ssData]
    }

    if(this.useShortcut){
      this.textProgress = 'Collecting Shortcut Icons'
      let sc = this.scData.map(i=>i.icons)
      let flatsc = sc.flat(Infinity)
      files = [...files, ...flatsc]
    }

    this.textProgress = 'Generating Files'
    files.forEach(i=>{
      if(typeof i.data != 'string'){
        zip.file(i.name, i.data, {
          binary:true
        })
      }
      else{
        zip.file(i.name, i.data)
      }
      this.textProgress= 'Creating '+i.name
    })

    
    this.textProgress = 'Completing Process'
    zip.generateAsync({type:'blob'})
    .then(blob=>{
      let date = new Date()
      this.textProgress = 'Writing Files'
      saveAs(blob, `WebEssential${date.toISOString().split('T')[0]}.zip`)
      this.progress=false
      this.submit = false
      this.done = true
    })

  }


},

computed:{
  basicError(){
    if(!this.basicData && this.submit) return true
    return false
  },

  relError(){
    if( this.useRelApp && !this.relAppData && this.submit) return true
    return false
  },

  iconError(){
      if(!this.file && this.submit & !this.done){
        if(!this.appIconData || !this.andIconData || !this.faviconData || !this.winIconData ){
          return true
        }
        return true
      }
      return false
  },

  ssError(){

      if(this.useScreenshot && !this.ssData && this.submit) return true
      return false
  },

  scError(){
      if(this.useShortcut && !this.scData && this.submit) return true
    return false
  },

  error(){
    if(this.basicError || this.iconError || this.scError || this.ssError || this.relError) return true
    return false
  }

},

watch:{
  file(val){
    if(val){
    let self = this
    reader(val).then(data=>{
      self.$refs.preview.src = data
    })
    }
  },

  done(val){
    if(val){
        this.file=undefined
        let self = this
        setTimeout(
          ()=>{
            this.done = false
            self.$forceUpdate()
          }, 1000
        )
      
    }
  }


},
}
</script>

