<template>
  <div>
      <h3>Shortcuts <span>ðŸ”œ</span></h3>
      <slot></slot>
      <div class="list">
          <div v-for="(val, i) in list" :key="i">
              <div class="form">
                  <strong>{{val.name}}</strong>
                  <button @click="()=>remove(val)" class="btn" style="width:max-content;background-color:var(--bg);"><span>âŒ</span> Remove</button>
              </div>
              <maskable auto :file="val.file" :name="val.name.replace(' ', '-')" 
              :dir="'img/icons/shortcut'" :sizes="[144, 192]"
              @complete="(r)=>{add_icon(r,i)}"/>
              <hr>
          </div>
      </div>
          <div class="form">
              <label for="name">Name</label>
              <input type="text" name="name" id="name" ref="name" required>
          </div>
          <div class="form">
              <label for="shortname">Short Name</label>
              <input type="text" name="shortname" id="shortname" ref="sname">
          </div>
          <div class="form">
              <label for="desc">Description</label>
              <input type="text" name="desc" id="desc" ref="desc">
          </div>
          <div class="form">
              <label for="shorcut">Relative URL</label>
              <input type="text" name="shorcut" id="shortcut" ref="url" required>
          </div>
          <div class="form">
              <label for="icon">Icon</label>
              <input type="file" name="icon" id="icon" accept="image/*" ref="file">
          </div>
          <div style="text-align:center;">
              <button @click="add" class="btn">Add</button>
          </div>
  </div>
</template>

<script>
import Maskable from './Maskable.vue'
export default {
    components:{
        Maskable,
    },
    data(){
        return{
            list:[],
            icon_result:[]
        }
    },
    props:{
        done:{
            type: Boolean,
            default: false
        }
    },

    methods:{

        add(){
            let url = this.$refs.url
            let file = this.$refs.file
            let name = this.$refs.name
            let sname = this.$refs.sname
            let desc = this.$refs.desc
            if(name.value && url.value && file.files[0]){
                this.list.push({
                    name: name.value,
                    file: file.files[0],
                    url: url.value,
                    sname: sname.value,
                    description: desc
                })
            url.value = file.value = name.value = sname.value = desc.value = ''
            }
        },

        remove(item){
            let l = this.list.filter(i=>i!=item)
            this.list = l
        },

        add_icon(result, index){
            this.icon_result[index] = result
        }
    },

    computed:{
        result(){
            let list = []
            for(let i =0; i<this.list.length; i++){
                list.push({
                    name: this.list[i].name,
                    sname: this.list[i].sname,
                    url: this.list[i].url,
                    icons: this.icon_result[i]
                })
            }
            return list
        }
    },

    watch:{
        done(val){
            if(val && this.list.length>0) this.$emit('complete', this.result)
        },
        icon_result(val){
            if(val.length!=0) this.$emit('complete', this.result)
        }
    }

}
</script>

