// <link rel="icon" type="image/png" sizes="16x16" href="/favicon-16x16.png">
// <link rel="manifest" href="/site.webmanifest">
// <link rel="mask-icon" href="/safari-pinned-tab.svg" color="#5bbad5">
// <meta name="msapplication-TileColor" content="#00aba9">
// <meta name="theme-color" content="#ffffbb"></meta>`

function generateMaskIcon(data){
    return `<link rel="mask-icon" href="${data.name.startsWith('./')?data.name:data.name.startsWith('/')?'.'+data.name:'./'+data.name}" color="${data.color}">`
}

function generateBrowserConfig(data){
    return `<meta name="msapplication-config" content="${data.name.startsWith('./')?data.name:data.name.startsWith('/')?'.'+data.name:'./'+data.name}">`
}

function generateIco(data){
    return `<link rel="icon" type="image/x-icon" href="${data.name.startsWith('./')?data.name:data.name.startsWith('/')?'.'+data.name:'./'+data.name}">`
}

function generateAppleIcon(data){
    return `<link rel="apple-touch-icon" sizes="${data.size}x${data.size}" href="${data.name.startsWith('./')?data.name:data.name.startsWith('/')?'.'+data.name:'./'+data.name}">`
}

function generateFavicon(data){
    return `<link rel="icon" type="${data.type}" sizes="${data.size}x${data.size}" href="${data.name.startsWith('./')?data.name:data.name.startsWith('/')?'.'+data.name:'./'+data.name}">`
}

function generateManifest(data) {
    return `<link rel="manifest" href="${data.name.startsWith('./')?data.name:data.name.startsWith('/')?'.'+data.name:'./'+data.name}">`
}

export function generate(list, info){
let boilerplate = `<!DOCTYPE html>
<html lang="${info.lang}">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <meta name="description" content="${info.description.replace('\n','. ')}">
    <meta name="keywords" content="${info.categories.toString()}">
    <title>${info.name}</title>

    <!-- Important -->
  `
  list.forEach(i => {
      boilerplate += '\t'
      boilerplate += i.name.endsWith('ico') ? generateIco(i) : i.name.endsWith('xml') ? 
      generateBrowserConfig(i) : i.name.endsWith('json') ? generateManifest(i) :
      i.name.endsWith('svg') ? generateMaskIcon(i) :
      i.name.includes('apple') ? generateAppleIcon(i) : generateFavicon(i)
      boilerplate += '\n'
  });
  boilerplate+=`<!-- Add these tags to your index.html file -->

</head>
<body>
  
</body>
</html>`
return boilerplate
}