<template>
      <select name="categories" id="categories" multiple v-model="selected" style="height: 15vh;">
          <option v-for="(val, i) in categories" :key="i"
          :value="val"
          > {{val.replace(val[0], val[0].toUpperCase())}}
          </option>
      </select>
</template>

<script>
export default {
    data(){
        return {
            categories:[
                'books',
                'business',
                'education',
                'entertainment',
                'finance',
                'fitness',
                'food',
                'games',
                'goverment',
                'health',
                'kids',
                'lifestyle',
                'magazines',
                'medical',
                'music',
                'navigation',
                'news',
                'personalization',
                'photo',
                'politics',
                'productivity',
                'security',
                'shopping',
                'social',
                'sports',
                'travel',
                'utilities',
                'weather',
            ],
            selected:[]
        }
    },

    watch:{
        selected(val){
            this.$emit('changed', val)
        }
    }

}
</script>

<style>
#categories{
    scrollbar-width: thin;
    scrollbar-color: var(--std) aliceblue;
}

#categories::-webkit-scrollbar{
    background-color: aliceblue;
    width: 8px;
}

#categories::-webkit-scrollbar-thumb{
background-color: var(--std);
border: 2px solid whitesmoke;
border-radius: 12px;
}


</style>