<template>
  <div v-if="file">
      <h4 class="preview">Windows Tile Icon</h4>
      <div ref="preview" class="preview"></div>
          <div class="form">
                  <label for="tilecolor">Tile Color</label>
                  <input type="color" name="color" id="tilecolor" v-model="color">
          </div>
          <hr>
  </div>
</template>

<script>
import Compressor from 'compressorjs'
export default {
  data(){
      return{
          tile:undefined,
          icon:undefined,
          color: '#5fb0ef',
          name: 'mstile',
          dir: 'img/icons'
      }
  },

  props:{
        file: File,
        done:{
            type: Boolean,
            default: false
            }
  },

  methods:{
      tileProcess(){
            let self = this
            let canvas = document.createElement('canvas')
            let ctx = canvas.getContext('2d')
            canvas.width = 270
            canvas.height = 270

            new Compressor(this.file,{
                width: 150,
                height: 150,
                strict: false,
                quality:1,
                checkOrientation: false,
                success(blob){
                        let url = window.URL.createObjectURL(blob)
                        let img = new  Image()
                        img.onload=()=>{
                            window.URL.revokeObjectURL(this.src)
                            ctx.drawImage(img, (canvas.width/2)-(img.width/2),(canvas.width/2)-(img.height/2))
                            canvas.toBlob(b=>{
                                b.arrayBuffer()
                                .then(buf=>{
                                    let data = {
                                        name: `${self.dir}/${self.name}-150x150.png`,
                                        data: new Uint8Array(buf),
                                        size: 150
                                    }
                                    self.tile = data
                                })
                            })
                        }
                    img.src = url
                }
            })

            
      },

      iconProcess(){
          let self = this
          new Compressor(this.file,{
            width: 144,
            height: 144,
            strict: false,
            quality:1,
            checkOrientation: false,
            success(blob){
                blob.arrayBuffer()
                .then(buf=>{
                    let data = {
                        name: `${self.dir}/${self.name}-144x144.png`,
                        data: new Uint8Array(buf),
                        size: 144
                    }
                    self.icon = data
                })
            }
          })
      },

      previewProcess(){
            let self = this
            let canvas = document.createElement('canvas')
            let ctx = canvas.getContext('2d')
            canvas.width = 128
            canvas.height = 128
            ctx.beginPath()
            ctx.rect(0,0,canvas.width, canvas.height)
            ctx.fillStyle = self.color
            ctx.fill()

          new Compressor(this.file,{
                width: 72,
                height: 72,
                strict: false,
                quality:1,
                checkOrientation: false, 
                success(blob){
                        let url = window.URL.createObjectURL(blob)
                        let img = new Image()
                        img.onload=()=>{
                            window.URL.revokeObjectURL(this.src)
                            ctx.drawImage(img, (canvas.width/2)-(img.width/2),(canvas.height/2)-(img.height/2))
                        }
                    img.src = url
                    if(self.$refs.preview.children.length==0) self.$refs.preview.appendChild(canvas)
                    else self.$refs.preview.replaceChild(canvas, self.$refs.preview.children[0] )
                }
            })

      },

      process(){
          if(this.file){
              this.tileProcess()
              this.iconProcess()
              this.previewProcess()
          }
      }

  },

  computed:{
      browserConfig(){
          return`<?xml version="1.0" encoding="UTF-8"?>
            <browserconfig>
                <msapplication>
                    <tile>
                        <square150x150logo src="./${this.tile.name}"/>
                        <TileColor>${this.color}</TileColor>
                    </tile>
                </msapplication>
            </browserconfig>`
      },


  },

  watch:{
      file(val){
          if(val) this.process()
      },

      color(){
          this.process()
      },

      tile(val){
          
        if(val && this.icon){  
          let bc = {
                      name: 'browserconfig.xml',
                      data: this.browserConfig
                      }
          let collective = [this.tile, this.icon, bc]
                    
          this.$emit('complete', collective)
            
          }
      }
  },

  mounted(){
    this.process()
  },

}
</script>

