<template>
  <div>
      <h3>Related Apps <span>ðŸ“²</span></h3>
      <slot></slot>
      <div v-for="(val, i) in data_platforms" :key="i" class="form">
                  <a :href="val.url" target="_blank">{{val.platform.toUpperCase()}}</a>

                  <button @click="()=>remove(val)" class="btn pfremove"><span>âŒ</span> Remove</button>
      </div>
      
      <div class="form table">
          <label for="platform">Platform</label>
          <select name="platform" id="platform" v-model="platform_selected">
              <option :value="undefined" disabled>Select Platform</option>
              <option :value="val.toLowerCase().replace(' ', '_')" v-for="(val, i) in platforms" :key="i"
              >{{val}}</option>
          </select>
      </div>
      <div class="form">
          <label for="url">URL</label>
          <input type="url" name="url" id="url" ref="url">
      </div>
      <div class="preview">
          <button @click="()=>add()" class="btn">Add</button>
      </div>
          
      <div class="form">
          <label for="prefer">Prefer Related App</label>
          <input type="checkbox" name="prefer" id="prefer" v-model="prefer">
      </div>
      <hr>
  </div>
</template>

<script>
export default {
    data(){
        return{
            prefer: false,
            platforms: [ 'Play', 'iTunes', 'Web App', 'Chrome Web Store', 'Windows'],
            platform_selected: undefined,
            data_platforms : []
        }
    },

    props:{
        done:{
            type: Boolean,
            default: false
        }
    },

    methods:{
        add(){
            if(this.platform_selected && this.$refs.url.value){
                this.data_platforms.push({
                    url:this.$refs.url.value,
                    platform: this.platform_selected
                })
                this.$refs.url.value = ''
                this.platform_selected = undefined
            }
        },

        remove(item){
            let list = this.data_platforms.filter(i=>i!=item)
            this.data_platforms = list
        }
    },

    watch:{
        data_platforms(val){
            if(val.length!=0) this.$emit('complete', {prefer_related_applications:this.prefer, related_applications: this.data_platforms})
        }
    }

}
</script>

<style scoped>
.pfremove{
    background-color: var(--bg);
    width: max-content;
}
</style>