<template>
  <div>
      <icon-processor auto :file="file" 
        :name="'apple-touch-icon'" :dir="'img/icons'" 
        :sizes="[60, 76, 120, 152, 180 ]"
        @complete="add_icon"/>
      <svg-processor :file="file" @complete="add_svg"/>
     <hr>
  </div>
</template>

<script>
import IconProcessor from './IconProcessor.vue'
import SvgProcessor from './SvgProcessor.vue'
export default {
  components: { IconProcessor, SvgProcessor },
  props:{
      file: File,
      done: {
          type: Boolean,
          default: false
      }
  },
  data(){
      return{
          icon:null,
          svg:null,
      }
  },
  computed:{
      icons(){
          return [...this.icon, this.svg]
      }
  },
  methods:{
      add_icon(val){
          this.icon = val
      },
      add_svg(val){
          this.svg = val
      }
  },
  watch:{
      svg(val){
          if(val){
              if(this.icon) this.$emit('complete', this.icons)
          } 
      },
      file(val){
          if(val){
              if(this.icon && this.svg) this.$emit('complete', this.icons)
          }
      }
  },


}
</script>

