<template>
  <div>
      <div class="form">
          <label for="appname">Name</label>
          <input type="text" name="appname" id="appname" v-model="name" placeholder="Name For Your App">
      </div>
      <div class="form">
          <label for="appshortname">Short Name</label>
          <input type="text" name="appshortname" id="appshortname" v-model="short_name" placeholder="Short Name For Your App">
      </div>
      <div class="form">
          <label for="appdescription">Description</label>
          <input type="text" name="appdescription" id="appdescription" v-model="description" placeholder="Your App Description">
      </div>
      <div class="form">
          <label for="scope">Scope</label>
          <input type="text" name="scope" id="scope" v-model="scope" placeholder="Application Scope Coverage">
      </div>
      <div class="form">
          <label for="starturl">Start URL</label>
          <input type="text" name="starturl" id="starturl" v-model="start_url" placeholder="Starting Point">
      </div>
      <div class="form">
          <label for="language">Language</label>
          <input type="text" name="language" id="language" v-model="lang" placeholder="ISO 639-1">
      </div>
      <div class="form">
          <label for="appdir">Direction</label>
          <select name="appdir" id="appdir" v-model="dir">
              <option v-for="(val, i) in dir_options" :key="'dir'+i"
              :value="val.replace(' ', '_').toLowerCase()"
              >{{val}}
              </option>
          </select>
      </div>
      <div class="form">
          <label for="appdisplay">Display</label>
          <select name="appdisplay" id="appdisplay" v-model="display">
              <option v-for="(val, i) in display_options" :key="'display'+i"
              :value="val.replace(' ', '_').toLowerCase()"
              >{{val}}
              </option>
          </select>
      </div>
      <div class="form">
          <label for="apporientation">Orientation</label>
          <select name="apporientation" id="apporientation" v-model="orientation">
              <option v-for="(val, i) in orientation_options" :key="'orient'+i"
              :value="val.replace(' ', '_').toLowerCase()"
              >{{val}}
              </option>
          </select>
      </div>
      <div class="form">
          <label for="themecolor">Theme Color</label>
          <input type="color" name="themecolor" id="themecolor" v-model="theme_color">
      </div>
      <div class="form">
          <label for="backgroundcolor">Background</label>
          <input type="color" name="backgroundcolor" id="backgroundcolor" v-model="background_color">
      </div>
      <div>
          <div class="form">
              <label style="align-self:start; margin-top:.5rem" for="categories">Category</label>
              <category-input @changed="add_categories"/>
          </div>
            
      </div>
      
  </div>
</template>

<script>
import CategoryInput from './CategoryInput.vue'
export default {
  components: { CategoryInput },
    data(){
        return{
            orientation_options:['Natural', 'Portrait', 'Landscape', 'Any'],
            orientation: 'natural',

            dir_options:['Auto', 'RTL', 'LTR'],
            dir: 'auto',

            display_options:['Standalone', 'Fullscreen', 'Browser', 'Minimal UI'],
            display: 'standalone',

            background_color: '#5fb0ef',
            theme_color: '#54c43e',

            description:"",
            lang:"en",
            categories: [],
            scope: '.',
            start_url: '/',
            name: '',
            short_name: '',


        }
    },

    props:{
        done:{
            type:Boolean,
            default: false
        }
    },

    methods:{
        add_categories(val){
            this.categories = val
        }
    },

    computed:{
        allData(){
            return [this.name, this.short_name, this.description, this.categories, this.background_color, this.start_url,
            this.theme_color, this.dir, this.orientation, this.display, this.description, this.lang, this.scope]
        }
    },

    watch:{
        allData(){
        if(this.name && this.short_name && this.description && this.lang && this.categories.length!=0){
            this.$emit('complete', { 
                name: this.name, 
                short_name: this.short_name,
                description: this.description,
                lang: this.lang,
                scope: this.scope,
                start_url: this.start_url,
                theme_color: this.theme_color,
                background_color: this.background_color,
                categories: this.categories,
                dir: this.dir,
                display: this.display,
                orientation: this.orientation
                })
        }
        }
    }

}
</script>

