<template>
    <div>
        <div>
         <h4 style="text-align:center">Android Maskable Icon</h4>
         <div class="preview" ref="preview"></div>
          <div class="form">
              <label for="padding">Resize</label>
              <input type="range" name="padding" id="padding" v-model="mask">
          </div>
            <div class="form">
                <label for="color">Background</label>
                <input type="color" name="color" id="color" v-model="color">
            </div>
            <div class="form">
                <label for="safezone">Safe Zone Area</label>
                <input type="checkbox" name="safezone" id="safezone" v-model="showSafeZone">
            </div>
        </div>
      <icon-processor :dir="dir" :sizes="sizes" :file="base" 
      :purpose="'maskable'"
      :name="name" auto @complete="add_result"/>
  </div>
</template>

<script>
import IconProcessor from './IconProcessor.vue'
import Compressor from 'compressorjs'

export default {
  components: { IconProcessor },
    props:{
        file: File,
        sizes:{
            type: Array,
            default:()=>[]
        },
        done:{
            type: Boolean,
            default: false
        },
        ui:{
            type: Boolean,
            default: true
        },
        dir:{
            type: String,
            default: ''
        },
        name:{
            type: String,
        },
        auto:{
            type:Boolean,
            default: false
        }

    },

    data(){
        return{
            result: [],
            base: undefined,
            setting:{
                previewSize: 128,
                size: 512,
            },
            mask:100,
            color:'#5fb0ef',
            showSafeZone: false,
        }
    },

    methods:{
        process(){
            if(!this.file) return
            this.$emit('processing')
            let self = this
                new Compressor(this.file,{
                width:this.mask,
                height:this.mask,
                maxWidth:this.mask,
                minWidth:this.mask,
                quality:1,
                strict:false,
                success(blob){
                    self.preview(blob, self)
                    }
                })   
                
                new Compressor(this.file,{
                width:this.mask*4,
                height:this.mask*4,
                maxWidth:this.mask*4,
                minWidth:this.mask*4,
                quality:1,
                strict:false,
                success(blob){
                    self.convert(blob, self)
                    }
                }) 
        },

        preview(blob, self){
            let canvas = document.createElement('canvas')
            let ctx = canvas.getContext('2d')
            canvas.width = self.setting.previewSize
            canvas.height = self.setting.previewSize
            ctx.beginPath()
            ctx.rect(0,0,self.setting.previewSize, self.setting.previewSize)
            ctx.fillStyle = self.color
            ctx.fill()

            let url = window.URL.createObjectURL(blob)
            let img = new  Image()
            img.onload=()=>{
                window.URL.revokeObjectURL(this.src)
                ctx.drawImage(img, (canvas.width/2)-(img.width/2),(canvas.width/2)-(img.height/2))
                if(self.showSafeZone){
                    ctx.beginPath()
                    ctx.arc(self.setting.previewSize/2, self.setting.previewSize/2 ,canvas.width*40/100, 0*Math.PI, 2*Math.PI)
                    ctx.strokeStyle = 'red'
                    ctx.stroke()
                }
            }
            img.src = url

            if(self.$refs.preview.children.length==0) self.$refs.preview.appendChild(canvas)
            else self.$refs.preview.replaceChild(canvas, self.$refs.preview.children[0] )
        },

        convert(blob, self){
            let canvas = document.createElement('canvas')
            let ctx = canvas.getContext('2d')
            canvas.width = self.setting.size
            canvas.height = self.setting.size

            ctx.beginPath()
            ctx.rect(0, 0, self.setting.size, self.setting.size)
            ctx.fillStyle = self.color
            ctx.fill()

            let url = window.URL.createObjectURL(blob)
            let img = new  Image()

            img.onload=()=>{
                window.URL.revokeObjectURL(this.src)
                ctx.drawImage(img, (canvas.width/2)-(img.width/2),(canvas.width/2)-(img.height/2))
                canvas.toBlob(b=>{
                    self.base = new File([b], 'mask.png', {
                        type:'image/png'
                    })
                })
                }
            img.src = url
        },
        add_result(val){
            this.result = val
        }
    },

    watch:{
        done(val){
            if(val){
                if (this.result.length == this.sizes.length){
                    this.$emit('complete', this.result)
                }
            }
        },

        file(val){
            if(val){
                this.process()
            }
        },

        color(){
            if(this.file) this.process()
        },
        mask(){
            if(this.file) this.process()
        },
        showSafeZone(){
            if(this.file) this.process()
        },
        result(val){
            if(this.auto){
                if (val.length == this.sizes.length){
                    this.$emit('complete', val)
                }
            }
        }


    },

    mounted(){
        if(this.file) this.process()
    }

}
</script>

