<template>
  <div v-if="result">
      <div class="preview">
          <h4>Safari Pinned Tab Icon</h4>
      <div v-html="result" ></div>
      </div>
      
        <div class="form">
            <label for="treshold">Treshold</label>
            <input type="range" name="" id="treshold" min="0" max="255" step="1" v-model.lazy="threshold" width="600px">    
        </div>
            <div class="form">
            <label for="color">Color</label>
            <input type="color" name="color" id="color" v-model="color">
        </div>
        <div class="form">
            <label for="invert">Invert</label>
            <input type="checkbox" name="" id="invert" v-model="invert">
        </div>            
      </div>
</template>

<script>
import Compressor from 'compressorjs'
import reader from '../scripts/FileReader'
import i2s from 'imagetracerjs'

export default {
    props: {
        file: File,
        dir: {
            type: String,
            default: ''
        }
    },

    computed:{
        colorValue(){
            let val = this.color   
            let r = Number(`0x${val[1]}${val[2]}`)
            let g = Number(`0x${val[3]}${val[4]}`)
            let b = Number(`0x${val[5]}${val[6]}`)
            return [r,g,b]
        }
    },

    data(){
        return {
            color: '#5fb0ef',
            threshold: 75,
            invert: false,
            result: '',
            datasvg:'',
            setting:{
                baseSize: 128,
                filename: 'safari-pinned-tab.svg'
            },
            svg: undefined
        }
    },

    methods:{
            prepareSVG(URI){
                return new Promise(function(resolve, reject) {
                    if (URI == null) return reject();
                    var canvas = document.createElement('canvas'),
                        context = canvas.getContext('2d'),
                        image = new Image();
                    image.addEventListener('load', function() {
                    canvas.width = image.width;
                    canvas.height = image.height;
                    context.drawImage(image, 0, 0, canvas.width, canvas.height);
                    resolve(context.getImageData(0, 0, canvas.width, canvas.height));
                    }, false);
                    image.src = URI;
                 });


                },


            previewSVG(){
                let self = this
                if(this.file){
                    return new Compressor(this.file, {
                        width: this.setting.baseSize,
                        height: this.setting.baseSize,
                        checkOrientation: false,
                        strict: false,
                        success(blob){
                            reader(blob)
                            .then(data=>{
                                if (data){
                                    let t = self.threshold
                                    self.prepareSVG(data).then(e=>{
                                    let len = e.data.length/4
                                    let r,g,b=0
                                    for(let i=0; i<len; i++){
                                            r= e.data[i*4+0]
                                            g= e.data[i*4+1]
                                            b= e.data[i*4+2]
                                        if((r+g+b)/3>t){
                                            if(self.invert){
                                            e.data[i*4+0]=self.colorValue[0]
                                            e.data[i*4+1]=self.colorValue[1]
                                            e.data[i*4+2]=self.colorValue[2]
                                            e.data[i*4+3]=255
                                            }
                                            else{
                                                e.data[i*4+0]=0
                                                e.data[i*4+1]=0
                                                e.data[i*4+2]=0
                                                e.data[i*4+3]=0
                                            }
                                        }
                                        else{
                                            if(self.invert){
                                            e.data[i*4+0]=0
                                            e.data[i*4+1]=0
                                            e.data[i*4+2]=0
                                            e.data[i*4+3]=0
                                            }
                                            else{
                                            e.data[i*4+0]=self.colorValue[0]
                                            e.data[i*4+1]=self.colorValue[1]
                                            e.data[i*4+2]=self.colorValue[2]
                                            e.data[i*4+3]=255
                                            }
                                        }
                                        if (t!=self.threshold) return
                                        
                                    }
                                    let svg = i2s.imagedataToSVG(e)
                                    self.result = svg
                                    })
                                }
                                            })
                                        }
                                    })
                                }
            },

            convertSVG(){
                let self = this
                if(this.file){
                    return new Compressor(this.file, {
                        width: this.setting.baseSize,
                        height: this.setting.baseSize,
                        checkOrientation: false,
                        strict: false,
                        success(blob){
                            reader(blob)
                            .then(data=>{
                                if (data){
                                    let t = self.threshold
                                    self.prepareSVG(data).then(e=>{
                                    let len = e.data.length/4
                                    let r,g,b=0
                                    for(let i=0; i<len; i++){
                                            r= e.data[i*4+0]
                                            g= e.data[i*4+1]
                                            b= e.data[i*4+2]
                                        if((r+g+b)/3>t){
                                            if(self.invert){
                                            e.data[i*4+0]=0
                                            e.data[i*4+1]=0
                                            e.data[i*4+2]=0
                                            e.data[i*4+3]=255
                                            }
                                            else{
                                                e.data[i*4+0]=0
                                                e.data[i*4+1]=0
                                                e.data[i*4+2]=0
                                                e.data[i*4+3]=0
                                            }
                                        }
                                        else{
                                            if(self.invert){
                                            e.data[i*4+0]=0
                                            e.data[i*4+1]=0
                                            e.data[i*4+2]=0
                                            e.data[i*4+3]=0
                                            }
                                            else{
                                            e.data[i*4+0]=0
                                            e.data[i*4+1]=0
                                            e.data[i*4+2]=0
                                            e.data[i*4+3]=255
                                            }
                                        }
                                        if (t!=self.threshold) return
                                        
                                    }
                                    let svg = i2s.imagedataToSVG(e)
                                    self.datasvg = svg
                                    let svgResult= {
                                        name: self.dir ? `${self.dir}/${self.setting.filename}`:self.setting.filename,
                                        data: svg,
                                        color: self.color
                                    }
                                    self.$emit('complete', svgResult)
                                    })
                                }
                                            })
                                        }
                                    })
                                }
            },

            processSVG(){
                this.$emit('processing')
                this.previewSVG()
                this.convertSVG()
            }
                

            
    },


    watch:{
        threshold(){
            if(this.file) this.processSVG()
        },

        invert(){
            if(this.file) this.processSVG()
        },

        color(){
            if(this.file) this.processSVG()
        },

        file(val){
            if(val) this.processSVG()
        },

        dir(n_val, o_val){
            this.svg.name.replace(o_val, n_val)
            this.$emit('complete', this.svg)
        },

    },

    mounted(){
        if(this.file)this.processSVG()
    }

}
</script>

<style>
section{
    text-align: center;
    position: relative;
}

section>menu>div{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    position: relative;
    width: 25%;
    margin: 5px;
}




section>menu>div>*{
    width: 100%;
}

section>menu{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    position: relative;
}

</style>