<template>
  <div></div>
</template>

<script>
import Compressor from 'compressorjs'
export default {
    data(){
        return {
            result: []
        }
    },

    props:{
        file: File,
        dir:{
            type: String,
            default: ''
        },
        sizes:{
            type: Array,
            default: ()=>[]
        },
        name:{
            type:String,
        },
        done:{
            type: Boolean,
            default: false
        },
        type:{
            type:String,
            default: 'image/png'
        },
        purpose:{
            type:String,
            default: 'any'
        },
        compression:{
            type: Number,
            default: 1
        },
        auto:{
            type: Boolean,
            default: false
        }
    },

    methods:{
        process(){
            if(this.file && this.name && this.sizes.length>0){
                this.$emit('processing')
                let self = this
                self.result = []
                this.sizes.forEach(size => {
                    new Compressor(self.file,{
                        width:size,
                        height:size,
                        strict: false,
                        checkOrientation: false,
                        quality:self.compression,
                        mimeType:self.type,
                        success(blob){
                            blob.arrayBuffer()
                            .then(buf=>{
                                self.result.push({
                                    name: self.dir?`${self.dir}/${self.name}-${size}x${size}.png`:`${self.name}-${size}x${size}.png`,
                                    data: new Uint8Array(buf),
                                    size: size,
                                    type: self.type,
                                    purpose: self.purpose
                                })
                            })
                        }
                    })
                });
            }
        }
    },

    watch:{
        file(val){
            if(val) this.process()
        },

        done(val){
            if(val){
                if(this.sizes.length==this.result.length){
                    this.$emit('complete', this.result)
                }
            }
        },

        result(r){
            if(this.auto){
                if(this.sizes.length==r.length){
                    this.$emit('complete', r)
                }
            }
        }
    }

}
</script>

