<template>
    <div>
        <icon-processor auto :file="file" 
        :name="'android-chrome'" 
        :sizes="sizes" :dir="'img/icons'"
        @complete="s_icon"/>
        <maskable auto :file="file" v-if="file"
        :name="'android-chrome-maskable'"
        @complete="m_icon" :sizes="sizes"
        :dir="'img/icons/maskable'"/>
        <hr v-if="file">
    </div>
</template>

<script>
import IconProcessor from './IconProcessor.vue'
import Maskable from './Maskable.vue'

export default {
  components: { IconProcessor, Maskable },
  data(){
      return {
        sizes: [36, 48, 72, 96, 144, 192, 256, 384, 512],
        standardicons: [],
        maskableicons:[]

      }
  },

  props:{
      file: File,
      done:{
          type: Boolean,
          default: false
      }
  },

  computed:{
      icons(){
          return [...this.maskableicons, ...this.standardicons]
      }
  },

  watch:{

      icons(val){
          if(val){
              this.$emit('complete', this.icons)
          }
      },
      
  },

  methods:{
      s_icon(val){
          this.standardicons = val
      },

      m_icon(val){
          this.maskableicons = val
      }

  }



  

}
</script>

