export function objectify_image(data){
    let obj = data.map(i=>{
        return {
            src: i.name.startsWith('./') ? i.name : i.name.startsWith('/') ? '.'+i.name : './'+i.name,
            sizes: `${i.size}x${i.size}`,
            type: i.type,
            purpose: i.purpose
        }
    })

    return {icons: obj}
}

export function objectify_shortcut(data){
    let obj = data.map(i=>{
        return {
            name: i.name,
            short_name : i.sname,
            url : i.url.startsWith('./') ? i.url : i.url.startsWith('/') ? '.'+i.url : './'+i.url,
            description: i.desc,
            ...objectify_image(i.icons)

        }
    })
    return {shotcuts: obj}
}

export function objectify_screenshot(data){
    let obj = data.map(i=>{
        return {
            src: i.name.startsWith('./') ? i.name : i.name.startsWith('/') ? '.'+i.name : './'+i.name,
            sizes: `${i.size}x${i.size}`,
            type: i.type,
        }
    })

    return {scheenshots: obj}
}