<template>
  <icon-processor :file="file" :sizes="sizes" :name="name" auto @complete="get_data"/>
</template>

<script>
import ico from '../scripts/IcoMaker'
import IconProcessor from './IconProcessor.vue'
export default {
  components: { IconProcessor },
    data(){
        return{
            result:[],
            sizes: [16, 32, 64, 128],
            name: 'favicon'
        }

    },


    methods:{
        get_data(val){
            this.result = val
        }
    },

    props:{
        file: File,

        done: {
            type: Boolean,
            default: false
        }
    },

    watch:{

        result(val){
            if(val){
                if(val.length == this.sizes.length){
                    let collective = this.result
                    let data = this.result.map(i=>i.data)
                    collective.push({
                        name: this.name+'.ico',
                        data: ico(data)
                    })
                    this.$emit('complete', collective)
                }
            }

        },
        done(val){
            if(val)this.$forceUpdate()
        }
    },
    


}
</script>

<style scoped>
    .preview{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    }

</style>